@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300display=swap");
.SearchContainer {
    position: relative;
    max-width: 267px;
    .search_icon {
        position: absolute;
        right: 20px;
        top: 20.5px;
    }
    .search {
        width: 267px;
        height: 56px;
        border: 1px solid rgba(26, 15, 9, 0.1);
        box-sizing: border-box;
        border-radius: 10px;
        padding: 14.45px 20px;
        font-family: "Open Sans", sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 150%;
        outline: none;
    }
}
